<script>
import LogoVue from './components/Logo.vue';
import StaticBlock from './components/UI/StaticBlock.vue';
export default {
  name: 'App',
  components: {
    LogoVue, StaticBlock
  },
  data() {
    return {
      itemsList: ['Arts and Crafts' , 'Bags', 'Clothes', 'Cosmetic', 'Sport and Fitness', 'Toys and Games', 'Jewelry', 'Judaica', 'Medical','Nutrition','Textile','Services'],
      selectedItems: ['Arts and Crafts', 'Clothes', 'Toys and Games'],
      companyName: '',
      link: '',
      content: '',
      files: []

    }
  },
  mounted() {
    fetch('/data.json')
    .then((res) => res.json())
    .then(res => {
      this.companyName = res.company_name;
      this.link = res.link;
      this.content = res.content;
      this.selectedItems = res.select_items
    })
  }
}
